.contact {
    min-height: 100vh;
    background-color: #262626;
    text-align: center;
    display: flex;
    justify-content: center;
    align-content: space-between;
    font-family: 'Lato', sans-serif;
}

.container {
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.title {
    color: #00aeef;
    font-size: 48px;
    font-weight: bold;
    margin-top: 20px;
}

.form {
    display: flex;
    flex-direction: column;
}

.inputName,
.inputEmail,
.textArea {
    width: 555px;
    height: 60px;
    background-color: #ececec;
    border: none;
    outline: none;
    text-transform: uppercase;
    padding: 0 0 0 30px;
    font-size: 14px;
    box-sizing: border-box;
    font-family: 'Lato', sans-serif;
}

.inputEmail {
    margin: 20px 0;
}

.textArea {
    height: 140px;
    padding: 25px 0 0 30px;
    font-weight: 400;
}

.btn {
    width: 555px;
    height: 60px;
    margin-bottom: 9%;
    background-color: #00aeef;
    color: #ffffff;
    border: none;
    outline: none;
    text-transform: uppercase;
    font-size: 16px;
    box-sizing: border-box;
    font-family: 'Lato', sans-serif;
    transition: 0.2s;
}

.btn:hover {
    color: #262626;
}

/*RESPONSIVE*/

@media (max-width: 576px) {
    .inputName,
    .inputEmail,
    .textArea,
    .btn {
        width: 280px;
    }
}

@media (min-width: 576px) {
    .inputName,
    .inputEmail,
    .textArea,
    .btn {
        width: 460px;
    }
}

@media (min-width: 1200px) {
    .inputName,
    .inputEmail,
    .textArea,
    .btn {
        width: 555px;
    }
}