.skill {
    width: 250px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: #ffffff;

}

.logo {
    width: 60px;
    margin-top: 40px;
}

.description {
    margin-bottom: 40px;
}

.title {
    font-size: 30px;
    color: #00aeef;
    font-weight: normal;
    padding-bottom: 10px;
}

.text {
    max-width: 300px;
    padding: 0 5px;
}

/*RESPONSIVE*/

@media (max-width: 576px) {
    .skill {
        margin: 10px 0;
    }
}

@media (min-width: 576px) {
    .skill {
        margin: 10px 0;
    }
}

@media (min-width: 768px) {
    .skill {
        max-width: 200px;
    }
}

@media (min-width: 992px) {
    .skill {
        max-width: 260px;
    }
}

@media (min-width: 1024px) {
    .skill {
        max-width: 280px;
        margin-bottom: 50px;
    }
}

@media (min-width: 1200px) {
    .skill {
        max-width: 350px;
    }
}
